.benefits {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: linear-gradient(0deg, #3a664a99, #b242abd5);
  padding-bottom: 100px;
}
.webImage {
  width: 298px;
  height: 193px;
  padding-bottom: 80px;
}

.benefits p {
  color: #f1f1f1;
  padding-top: 20px;
  font-size: 1.4em;
}

.benefitTitle {
  font-size: 2em;
  font-weight: 400;
  color: #f1f1f1;
}
